body{
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h2,h3{
    margin: 0;
}

button {
    color: #009;
    font: inherit;
    border: 0;
    background: transparent;
    cursor: pointer;
}

.App {
    width: 95%;
    max-width: 700px;
    margin: 0 auto;
}

.App header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.App h1:after {
    content: 'üêæ';
    margin-left: 0.5em;
}

.App nav button:before{
    content: '‚¨Ö';
    margin-right: 0.5em;
}

.App main {
    display: flex;
}

.Profile{
    display: flex;
    flex-grow: 1;
    width: 66%;
}

.Profile .profile-picture {
    width: 100px;
    height: 100px;
    margin-right: 1rem;
    display: flex;
    background: #999;
}

.Profile .profile-picture img {
    width: inherit;
}

.Profile.loading {
    color: #999;
}

.Profile.loading p{
    background: #999;
}

.Directory{
    flex-grow: 1;
}